.project-sec {
  background-color: #1a1a1a; /* dark */
  background-image: url("../assets/project.png");
  background-repeat: repeat;
  background-size: auto;
  color: #fff;
  position: relative;
  padding: 20px 20px;
  color: white;
  margin-top: 10px;
  z-index: 1;
  min-height: 100vh;
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 1s ease-out, transform 1s ease-out;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.project-sec.fade-in {
  opacity: 1;
  transform: translateY(0);
}

.project-sec::before {
  content: "";
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: rgba(28, 26, 31, 0.4);
  z-index: -1;
}

.project-sec h2 {
  font-size: 1.7rem;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: #ffffff;
  text-shadow: 1px 1px 4px rgba(0,0,0,0.6);
}

.project-list {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  box-shadow: 0 1px 5px rgba(0,0,0,0.2);
  padding: 1.5rem;
  margin-bottom: 2rem;
  width: 90%;
  max-width: 800px;
  color: #f0f0f0;
}

.project-list h3 {
  font-size: 1.4rem;
  font-weight: 600;
  color: #ffffff;
  border-bottom: 2px solid #ffc2e8;
  padding-bottom: 5px;
  margin-bottom: 1rem;
  text-shadow: 1px 1px 3px rgba(0,0,0,0.5);
}

.project-list p,
.project-list ul li {
  font-size: 1rem;
  font-weight: 400;
  margin: 0.3rem 0;
  text-align: left;
  color: #eaeaea;
}

.project-list strong {
  font-weight: 600;
  color: #ffffff;
}

.project-list span {
  font-weight: 400;
  display: block;
  margin-top: 0.2rem;
  color: #dcdcdc;
}

/* 📱 Responsive tweaks */
@media (max-width: 600px) {
  .project-sec {
    padding: 1rem;
  }

  .project-list h3 {
    font-size: 1.2rem;
  }

  .project-list p,
  .project-list ul li {
    font-size: 0.95rem;
  }
}