html {
  scroll-behavior: smooth;
}
.resumeintro {
  background-color: #0d0d0d; /* dark base */
  background-image: url("../assets/Intro.png");
  background-repeat: repeat;
  background-size: auto;
  color: #fff; /* text contrast */
  font-family: "Poppins", sans-serif;
  font-weight: 100;
  font-style: italic;
  animation: gradientMove 10s ease infinite;
  position: relative;
  text-align: center;
  z-index: 1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

  @keyframes gradientMove {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Overlay for readability */
.resumeintro::before {
  content: "";
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: -1;
}

/* Profile image */
.ProfilePic {
  opacity: 0;
  transform: scale(0.7);
  animation: fadeInZoom 1.2s ease forwards;
  width:60vh;
  height: 60vh;
  object-fit: cover;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
  margin-bottom: 20px;
  transition: transform 0.5s ease;
}
@keyframes fadeInZoom {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.ProfilePic:hover {
  transform: scale(1.05);
}

/* Profile text */
.profile h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  border-right: 3px solid #fff;
  width: 0;
  animation: typing 2s steps(20) forwards, blink 0.7s step-end infinite, hideCursor 0s 0s forwards;
  text-shadow: 1px 1px 5px rgba(0,0,0,0.7);
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

@keyframes blink {
  50% { border-color: transparent }
}

/* Know More button */
.knowmore {
  padding: 10px 10px;
  font-size: 0.8rem;
  background-color: #500096;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.knowmore:hover {
  background-color: #9b79f1;
}

/* Fade-in animation */
.resumeintro[data-fade] {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 1s ease-out, transform 1s ease-out;
}

.resumeintro.fade-in {
  opacity: 1;
  transform: translateY(0);
}
.full-width {
  position: fixed;
  top: 0;
  right: 0; /* ✅ Align to right */
  width: 250px;
  height: 100vh;
  background: rgba(0, 0, 0, 0.85);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  overflow-y: auto;
  box-shadow: -2px 0 10px rgba(0,0,0,0.3);
  transform: translateX(100%);
  transition: transform 0.4s ease;

}
.full-width.show {
  transform: translateX(0);
}

.side-image {
  width: 150px;
  height: 160px;
  border-radius: 30%;
  object-fit: cover;
  margin-top: 30px;
  box-shadow: 0 0 8px rgba(255,255,255,0.4);
  transition: transform 0.3s ease;
}

.side-image:hover {
  transform: scale(1.05);
}
.icon-menu {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1001;
  cursor: pointer;
}
.social-icons-container {
  position: relative;
  z-index: 1000;
}
.menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5); /* Dim effect */
  z-index: 998;
}
.menu-bar{
  margin-left: 20px;
  padding: 10px;
}
.menu-bar a{
  font-size: small;
  text-decoration: none;
  color: inherit;
  color: blueviolet;
  transition: transform 0.3s ease;

}
.menu-bar a:hover{
  line-height: 40px;
  height : 50px;
  width: 50px;
  padding: 5px;
  border-radius: 20%;
  background-color: rgb(219, 210, 244);
  background-size: cover;
  transform: scale(1.05);
  
}
.menu-bar li{
  padding: 10px 5px;
  text-align: left;
  color: azure;
}
.social-icons{
  justify-items: center;
  display: flex;
  flex-direction: row;
  margin-top: 15px;
  padding: 30px 40px;
  gap: 30px;
}
.icon-linkedin:hover{
  line-height: 40px;
  height : 40px;
  width: 40px;
  border-radius: 50%;
  background-color: rgb(219, 210, 244);
  background-size: contain;
}
.icon-github:hover{
  line-height: 40px;
  height : 40px;
  width: 40px;
  border-radius: 50%;
  background-color: rgb(219, 210, 244);
  background-size: contain;
}
.icon-email:hover{
  line-height: 40px;
  height : 40px;
  width: 40px;
  border-radius: 50%;
  background-color: rgb(219, 210, 244);
  background-size: contain;
}
.floating-icons {
  display: flex;
  gap: 20px;
  margin-top: 10px;
  margin-bottom: 20px;
  margin-left: 35px;
  font-size: 2rem;
  color: #fff;
  position: relative;
}

.floating-icons span {
  display: inline-block;
  animation: float 3s ease-in-out infinite;
}

.floating-icons span:nth-child(2) {
  animation-delay: 0.3s;
}
.floating-icons span:nth-child(3) {
  animation-delay: 0.6s;
}
.floating-icons span:nth-child(4) {
  animation-delay: 0.9s;
}
.floating-icons span:nth-child(5) {
  animation-delay: 1.2s;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}

