.skills-sec {
  background-color: #ffffff;
  background-image: url("../assets/Skills\ \(2\).png");
  background-repeat: repeat;
  background-size: auto;
  color: #000;
  position: relative;
  padding: 30px 20px;
  color: white;
  z-index: 1;
  min-height: 100vh;
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 1s ease-out, transform 1s ease-out;
}

.skills-sec.fade-in {
  opacity: 1;
  transform: translateY(0);
}

.skills-sec::before {
  content: "";
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: -1;
}

/* Headings */
.skills-sec h1{
  font-size: 1.7rem;
  color: #ffffff;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-shadow: 1px 1px 4px rgba(0,0,0,0.6);
}
.skills-sec h2 {
  font-size: 1.3rem;
  border-bottom: 2px solid #ffc2e8;
}
/* Skill container */
.full-skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-top: 20px;
}

/* Skill lists */
.skills-list, .skills-list-2 {
  flex: 1 1 300px;
  max-width: 400px;
}

/* Skill items */
.skills-list li, .skills-list-2 li {
  background-color: #ffffff;
  color: #333;
  padding: 0.6rem 1rem;
  border-radius: 20px;
  display: flex;
  align-items: center;
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
  transition: background-color 0.3s ease;
}

.skills-list li:hover, .skills-list-2 li:hover {
  background-color: #592b7a;
  color: white;
  cursor: default;
  padding: 15px;
  line-height: auto;
}

.skills-list li svg, .skills-list-2 li svg {
  margin-right: 0.5rem;
  flex-shrink: 0;
}

/* 📱 Responsive tweaks */
@media (max-width: 600px) {
  .skills-sec {
    padding: 1rem;
  }

  .skills-list li, .skills-list-2 li {
    font-size: 0.95rem;
    flex: 1 1 100%;
    justify-content: center;
  }
}