.edu-sec section{
  margin-top: 10px;
}
.edu-sec{
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 40px 20px;
  z-index: 1;
  font-size: 2rem;
  background-image: url("../assets/Education\ \(2\).png");
  background-repeat: repeat;
  background-size: auto;
  color: #ffffff;;
  margin-top: 10px;
}
.edu-sec::before {
  content: "";
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: rgba(24, 0, 24, 0.4); /* dark overlay for readability */
  z-index: -1;
}
.edu-sec h1 {
  font-size: 1.9rem;
  padding-bottom: 5px;
  margin-bottom: 1rem;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.edu-list, .edu-inst {
  list-style-type: none;
  margin-bottom: 1.5rem;
  padding: 1rem;
  margin: 10px auto;
  border: 1px solid #ccc;
  border-radius: 6px;
  text-align: left;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  max-width: 90%;
}
.edu-sec h3{
  font-size: 1.3rem;
    padding-bottom: 10px;
    border-bottom: 2px solid #ffc2e8;
}
.edu-sec p strong:hover, .edu-sec p span:hover {
  background-color: #efe2fc;
  color: black;
  cursor: default;
  width: 100%;
  padding: 7px;
  line-height: 2px;
  border-radius: 20px;
}

.edu-sec p strong {
  font-size: 1.2rem;
  color: #f0eded;
  font-weight: 200;
}

.edu-sec p span {
  font-weight: 200;
  padding: 5px;
  color: #fefdfd;
  font-size: 1.1rem;
}
/* 📱 Mobile Tweaks */
@media (max-width: 768px) {
  .edu-sec {
    padding: 50px 15px;
    margin: 0 auto;
  }

  .edu-list,
  .edu-inst {
    text-align: center;
    font-size: 1rem;
  }
  .edu-sec h2{
    font-size: 1.5rem;
  }
}
